___CSS_LOADER_EXPORT___.push([module.id, ".badge-position[data-v-730b2d64]{left:4rem;position:relative;top:-5rem}.darkgray-italic[data-v-730b2d64]{color:#a9a9a9;font-style:italic}.black-italic[data-v-730b2d64]{color:#000;font-style:italic}.red-italic[data-v-730b2d64]{color:red;font-style:italic}", "",{"version":3,"sources":["webpack://./resources/js/Pages/Admin/Teams.vue"],"names":[],"mappings":"AA6MA,iCAEE,SAAU,CADV,iBAAkB,CAElB,SACF,CAEA,kCACE,aAAe,CACf,iBACF,CAEA,+BACE,UAAY,CACZ,iBACF,CAEA,6BACE,SAAU,CACV,iBACF","sourcesContent":["<template>\n  <Head title=\"Admin/Teams\"/>\n\n  <div class=\"place-self-center flex flex-col gap-y-3\">\n    <div id=\"topDiv\" class=\"bg-white text-black dark:bg-gray-800 dark:text-gray-50 p-5 mb-10\">\n\n      <Message v-if=\"appSettingStore.showFlashMessage\" :flash=\"$page.props.flash\"/>\n\n      <AdminHeader>Teams</AdminHeader>\n\n      <button\n          @click=\"appSettingStore.btnRedirect(`/teams/create`)\"\n          class=\"bg-green-600 hover:bg-green-500 text-white px-4 py-2 rounded disabled:bg-gray-400\"\n      >Create Team (needs to go to a new page: Admin/teams/create)\n      </button>\n\n      <div class=\"flex flex-row justify-end gap-x-4\">\n        <input v-model=\"search\" type=\"search\" placeholder=\"Search...\" class=\"text-black border px-2 rounded-lg\"/>\n      </div>\n\n\n      <div class=\"flex flex-col\">\n        <div class=\"-my-2 overflow-x-auto sm:-mx-6 lg:-mx-8\">\n          <div class=\"py-2 align-middle inline-block min-w-full sm:px-6 lg:px-8\">\n            <div class=\"shadow overflow-hidden border-b border-gray-200 sm:rounded-lg\">\n\n\n              <div class=\"overflow-hidden bg-white dark:bg-gray-800 shadow-sm sm:rounded-lg\">\n                <div class=\"p-6 bg-white dark:bg-gray-800 border-b border-gray-200\">\n                  <!-- Paginator -->\n                  <Pagination :data=\"teams\" class=\"mb-6\"/>\n                  <div\n                      class=\"relative overflow-x-auto shadow-md sm:rounded-lg\"\n                  >\n\n\n                    <table\n                        class=\"w-full text-sm text-left text-gray-500 dark:text-gray-400 overflow-x-auto\"\n                    >\n                      <thead\n                          class=\"text-xs text-gray-700 uppercase bg-gray-50 dark:bg-gray-700 dark:text-gray-400\"\n                      >\n                      <tr>\n                        <th scope=\"col\" class=\"min-w-[8rem] px-6 py-3\">\n                          Logo\n                        </th>\n                        <th scope=\"col\" class=\"px-6 py-3\">\n                          Team Name\n                        </th>\n                        <th scope=\"col\" class=\"px-6 py-3\">\n                          Team Creator\n                        </th>\n                        <th scope=\"col\" class=\"px-6 py-3\">\n                          # of Members\n                        </th>\n                        <th scope=\"col\" class=\"px-6 py-3\">\n                          # of Shows\n                        </th>\n                        <th v-if=\"props.can.viewCreator\" scope=\"col\" class=\"px-6 py-3\">\n                          <!--Manage/Edit-->\n                        </th>\n                      </tr>\n                      </thead>\n                      <tbody>\n                      <tr\n                          v-for=\"team in teams.data\"\n                          :key=\"team.id\"\n                          class=\"bg-white border-b dark:bg-gray-800 dark:border-gray-700\"\n                      >\n                        <th\n                            scope=\"row\"\n                            class=\"min-w-[8rem] px-6 py-4 font-medium text-gray-900 dark:text-white whitespace-nowrap\"\n                        >\n\n                          <Link :href=\"`/teams/${team.slug}`\" class=\"\">\n                            <SingleImage :image=\"team.image\" :poster=\"team.logo\" :alt=\"'show cover'\"\n                                         class=\"rounded-full h-20 w-20 object-cover\"/>\n                          </Link>\n                          <span\n                              :class=\"`badge ${getBadgeColor(team.status.id)} text-white font-semibold flex items-center justify-center`\"\n                              class=\"relative badge-position\">{{ team.status.status }}</span>\n                        </th>\n                        <th\n                            scope=\"row\"\n                            class=\"px-6 py-4 font-medium text-gray-900 dark:text-white whitespace-nowrap\"\n                        >\n                          <Link :href=\"`/teams/${team.slug}`\"\n                                class=\"text-blue-800 hover:text-blue-600 dark:text-blue-200 dark:hover:text-blue-400\">\n                            {{ team.name }}\n                          </Link>\n                        </th>\n                        <th\n                            scope=\"row\"\n                            class=\"px-6 py-4 font-medium text-gray-900 dark:text-white whitespace-nowrap\"\n                        >\n                          {{ team.teamCreator }}\n                        </th>\n                        <td\n                            scope=\"row\"\n                            class=\"px-6 py-4 font-medium text-gray-900 dark:text-white whitespace-nowrap\"\n                        >\n                          {{ team.memberSpots }}\n                        </td>\n                        <td\n                            scope=\"row\"\n                            class=\"px-6 py-4 font-medium text-gray-900 dark:text-white whitespace-nowrap\"\n                        >\n                          {{ team.totalShows }}\n                        </td>\n                        <td class=\"px-6 py-4 space-x-2\">\n                          <button\n                              v-if=\"team.can.viewTeam\"\n                              @click=\"appSettingStore.btnRedirect(`/teams/${team.slug}/manage`)\"\n                              class=\"px-4 py-2 text-white bg-purple-600 hover:bg-purple-500 rounded-lg\"\n                          >Manage\n                          </button>\n                          <button\n                              v-if=\"team.can.editTeam\"\n                              @click=\"appSettingStore.btnRedirect(`/teams/${team.slug}/edit`)\"\n                              class=\"px-4 py-2 text-white bg-blue-600 hover:bg-blue-500 rounded-lg\"\n                          >Edit\n                          </button>\n                        </td>\n                      </tr>\n                      </tbody>\n                    </table>\n                    <!-- Paginator -->\n                    <Pagination :data=\"teams\" class=\"pb-6\"/>\n                  </div>\n                </div>\n              </div>\n\n            </div>\n          </div>\n        </div>\n      </div>\n\n    </div>\n  </div>\n\n</template>\n\n<script setup>\nimport { Inertia } from \"@inertiajs/inertia\"\nimport { ref, watch } from \"vue\"\nimport throttle from \"lodash/throttle\"\nimport { usePageSetup } from '@/Utilities/PageSetup'\nimport { useAppSettingStore } from \"@/Stores/AppSettingStore\"\n\nimport AdminHeader from \"@/Components/Pages/Admin/AdminHeader\"\nimport Pagination from \"@/Components/Global/Paginators/Pagination\"\nimport Message from \"@/Components/Global/Modals/Messages\"\nimport SingleImage from \"@/Components/Global/Multimedia/SingleImage\"\n\nusePageSetup('adminTeams')\n\nconst appSettingStore = useAppSettingStore()\n\nlet props = defineProps({\n  teams: Object,\n  filters: Object,\n  can: Object,\n});\n\nlet search = ref(props.filters.search)\n\nwatch(search, throttle(function (value) {\n  Inertia.get('/admin/teams', {search: value}, {\n    preserveState: true,\n    replace: true\n  });\n}, 300));\n\nfunction getBadgeColor(statusId) {\n  switch (statusId) {\n    case 1:\n      return 'bg-green-500';     // Green\n    case 2:\n      return 'bg-blue-500';      // Blue\n    case 3:\n      return 'bg-purple-500';    // Purple\n    case 4:\n      return 'bg-orange-500';    // Orange\n    case 5:\n      return 'bg-red-500';       // Red\n    case 6:\n      return 'darkgray-italic';  // Custom class\n    case 7:\n      return 'black-italic';     // Custom class\n    case 8:\n      return 'black-italic';     // Custom class\n    case 9:\n      return 'red-italic';       // Custom class\n    case 10:\n      return 'red-italic';      // Custom class\n    case 11:\n      return 'bg-gray-700';     // Dark gray\n    default:\n      return 'bg-gray-300';     // Default color\n  }\n}\n\n</script>\n\n<style scoped>\n.badge-position {\n  position: relative;\n  left: 4rem; /* Move to the right */\n  top: -5rem; /* Move upwards */\n}\n\n.darkgray-italic {\n  color: darkgray;\n  font-style: italic;\n}\n\n.black-italic {\n  color: black;\n  font-style: italic;\n}\n\n.red-italic {\n  color: red;\n  font-style: italic;\n}\n</style>\n\n"],"sourceRoot":""}]);
